import 'bootstrap/js/src/util';
import 'bootstrap/js/src/alert';
import 'bootstrap/js/src/button';
import 'bootstrap/js/src/collapse';
import 'bootstrap/js/src/dropdown';
import 'bootstrap/js/src/modal';
import 'bootstrap/js/src/popover';
import 'bootstrap/js/src/tab';
import 'bootstrap-touchspin';
import './lib/slick.min';
import './lib/background-check.min';
import './lib/jquery.hoverIntent.min';

import BackgroundCheck from './lib/background-check.min';
import prestashop from 'prestashop';
import EventEmitter from 'events';
import lazysizes from 'lazysizes';

import SlickSlider from './components/slick';
import Form from './components/form';
import TopMenu from './components/top-menu';
import Utils from './components/utils';
import './components/darkmode';
import './components/block-cart';

import './checkout';
import './customer';
import './listing';
import './product';
import './cart';

// "inherit" EventEmitter
for (var i in EventEmitter.prototype) {
  prestashop[i] = EventEmitter.prototype[i];
}

$(document).ready(() => {
	console.log(prestashop);
	
  const form = new Form();
	const $body = $('body');
  let slickSlider = new SlickSlider();
  let topMenuEl = $('#_desktop_top_menu #top-menu');
  let topMenu = new TopMenu(topMenuEl);
	let scrollbarWidth = (window.innerWidth - document.documentElement.clientWidth) + 'px';
	document.documentElement.style.setProperty('--scroll-width', scrollbarWidth);

  form.init();
  slickSlider.init();
  topMenu.init();
  Utils.createSpin();
	
	
	if ('serviceWorker' in navigator) {
		navigator.serviceWorker
		.register(prestashop.urls.theme_assets + 'js/service-worker.php', {
			scope: '/'
		})
		.then(function(registration) {
			console.log('Registration successful, scope is:', registration.scope);
		})
		.catch(function(error) {
			console.log('Service worker registration failed, error:', error);
		});
	}

  //display input file content in custom file input BS
  $('.custom-file-input').on('change',function(){
      let fileName = $(this).val().split('\\').pop();
      $(this).next('.custom-file-label').addClass("selected").html(fileName);
  });
	
	let headerModal = $(`#header-modal`) || {};
	headerModal.modal('hide');
	$body.on(
      'click',
      '#nav-menu',
      (event) => {
          event.stopPropagation();
					headerModal.modal('show');
          return false;
      }
  );
	
	let psCustomersigninModal = $(`#ps-customersignin-modal`) || {};
	psCustomersigninModal.modal('hide');
	$body.on(
      'click',
      '#nav-log',
      (event) => {
          event.stopPropagation();
					psCustomersigninModal.modal('show');
          return false;
      }
  );
	
	let psLanguageselectorModal = $(`#ps-languageselector-modal`) || {};
	psLanguageselectorModal.modal('hide');
	$body.on(
      'click',
      '#nav-lang',
      (event) => {
          event.stopPropagation();
					psLanguageselectorModal.modal('show');
          return false;
      }
  );
	
	let psShoppingcartModal = $('#ps-shoppingcart-modal');
	psShoppingcartModal.modal('hide');
	$body.on(
      'click',
      '#nav-cart',
      (event) => {
          event.stopPropagation();
					$('#ps-shoppingcart-modal').modal('show');
          return false;
      }
  );
  let psFacetedsearchModal = $('#ps-facetedsearch-modal');
	psFacetedsearchModal.modal('hide');
	$body.on(
      'click',
      '#nav-fltr',
      (event) => {
          event.stopPropagation();
					$('#ps-facetedsearch-modal').modal('show');
          return false;
      }
  );
	
	$(document).on(
      'swipe',
      (event) => {
          console.log($('.modal-open').scrollTop());
          return false;
      }
  );
	
	try {
		BackgroundCheck.init({
			targets: '.text'
		});
	}
	catch(err) {
		console.log("BGcheck: " + err);
	}
  
  $(document).on( 'click', '.slider > div:nth-child(2)', () => {
    $('.slider > div:last-child').insertBefore('.slider > div:first-child');
  });
  $(document).on( 'swipeleft', () => {
    $('.slider > div:first-child').insertAfter('.slider > div:last-child');
  });

  $(document).on( 'click', '.slider > div:nth-child(4)', () => {
    $('.slider > div:first-child').insertAfter('.slider > div:last-child');
  });
  $(document).on( 'swiperight', () => {
    $('.slider > div:last-child').insertBefore('.slider > div:first-child');
  });
  
});
document.addEventListener('lazyloaded', function(e){
    $(e.target).parent().addClass('rc--lazyload');
});